<template>
  <div class="navbar navbar-expand-lg navbar-light bg-light" id="translateForm">
  	<div class="form-inline my-2 my-lg-0">
	    <form id="transForm" class="well form-inline" v-on:submit="formSubmit">
	    	<input class="form-control mr-sm-2" type="text" v-model="textToTranslate" placeholder="输入需要翻译的内容!">
	    	<select v-model="language" class="btn-group-vertical">
	    		<option value="en" class="btn btn-primary">English</option>
	    		<option value="ru" class="btn btn-primary">Russian</option>
	    		<option value="ko" class="btn btn-primary">Korean</option>
	    		<option value="ja" class="btn btn-primary">Japanese</option>
	    	</select>
	    	<input class="btn btn-secondary my-2 my-sm-0" type="submit" value="翻译">
	    </form>
    </div>
  </div>
</template>

<script>


export default {
  name: 'translateForm',
  data: function(){
  	return{
		textToTranslate: '',
		language: ''
  	}
  },
  methods:{
  	formSubmit:function(e){
  		// console.log(this.textToTranslate);
  		this.$emit("formSubmit",this.textToTranslate,this.language);
  		e.preventDefault();
  	}
  },
  created:function(){
  	this.language = "en";
  }
  
}
</script>

<style>
	#transForm{
		border-radius: 10px;
		border: 1px #ccc solid;
	}
</style>



